.logo {
  text-align: center;
}

.list {
  width: 300px;
  list-style: none;
  margin: 0 auto;
  padding: 0;
  text-align: center;
}

.listItem {
  margin-bottom: 20px;
}

.contacts {
  text-align: center;
}

.map {
  text-align: center;
}

.footer {
  position: absolute;
  bottom: 20px;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 50px;
  text-align: center;
}

@media (min-width: 768px) {
  .list {
    width: 100%;
    display: flex;
    justify-content: space-around;
  }

  .listItem {
    width: 28%;
  }

  .footer {
    flex-direction: row;
    justify-content: flex-end;
    padding-top: 10px;
    padding-right: 30px;
    align-self: flex-end;
    flex: 0 0 100%;
    bottom: 0;
    right: 0;
  }
}
